import { type NextRequest, NextResponse } from "next/server"

// Mock categories
const categories = [
  { id: 1, user_id: 1, name: "Fruits & Vegetables", color: "#10B981" },
  { id: 2, user_id: 1, name: "Dairy & Eggs", color: "#F59E0B" },
  { id: 3, user_id: 1, name: "Meat & Fish", color: "#EF4444" },
  { id: 4, user_id: 1, name: "Pantry", color: "#8B5CF6" },
]

export async function GET(request: NextRequest) {
  try {
    const userCategories = categories.filter((cat) => cat.user_id === 1)
    return NextResponse.json(userCategories)
  } catch (error) {
    return NextResponse.json({ error: "Failed to fetch categories" }, { status: 500 })
  }
}
